import React, {Component} from 'react';
import {  Modal,ModalHeader, ModalBody, ModalFooter } from 'reactstrap';

class Mode extends Component{
    constructor(props){
        super(props);
        this.state = {
            modal : false,
        }
        this.toggle = this.toggle.bind(this);
    }
    toggle(){
        this.setState({
            modal : !this.state.modal
        })
    }

// profile (){
//     this.toggle();
// }

    render(){
        console.log('+++++++++++++++++++',this.state.modal);
        return(
            <div>
                 <button onClick={this.toggle} className="btn btn-primary pull-right" >profile</button>
                    <Modal isOpen={this.state.modal} toggle={this.toggle} >
                    <ModalHeader toggle={this.toggle}>Modal title</ModalHeader>
                    <ModalBody>
                    <div className="form-group">
                    <label>USerName:  </label>
                    <input type="text" name="username"   value={this.state.username} readOnly/>
                    </div>
                    <div className="form-group">
                    <label>UserEmail: </label>
                    <input type="text" name="useremail" className="form-control" value={this.state.useremail} readOnly/>
                    </div>      
                    <div className="form-group">
                    </div>
                    <div style={{ marginTop: 10 }}>

                    </div>
                    </ModalBody>
                    <ModalFooter>

                    </ModalFooter>
                    </Modal>
            </div>
        )
    }
}

export default Mode;